body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html, body{
  width: 100%;
  max-width: 100vw;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  margin: 0;
  padding: 0;
}

html, body {
  width: 100%;
  max-width: 100vw;
  height: 100%;
}

body {
  background-color: #f5f6fa;
}

.cell {
  display: none;
}

@media (max-width: 900px) {
  .card {
    display: flex !important;
    flex-direction: column !important;
    width: 90% !important;
  }
  .reply {
    width: 88% !important;
    justify-content: initial !important;
    align-items: initial !important;
    padding-left: initial !important;
  }
  .reply div {
    width: 95% !important;
  }
  .reply::before {
    height: auto !important;
  }
  .cell {
    display: flex;
    width: 100%;
    justify-content: space-around !important;
  }
  .sone, .Breply {
    display: none !important;
  }
  .cell .sone, .cell .Breply {
    display: flex !important;
  }
  .comentar {
    width: 90% !important;
  }
  .sone {
    width: 30% !important;
    flex-direction: row !important;
  }
  div .sone {
    width: 40% !important;
  }
  .sone button {
    border: 0px !important;
    height: -10.5em !important;
    border-top-left-radius: 10px !important;
    border-top-right-radius: initial !important;
    border-bottom-left-radius: 10px !important;
    border-bottom-right-radius: initial !important;
    color: #5457b6 !important;
    padding-bottom: 20px !important;
    padding-top: 10px !important;
    width: 40% !important;
  }
  .sone span + button {
    border: 0px !important;
    height: -10.5em !important;
    border-top-left-radius: initial !important;
    border-top-right-radius: 10px !important;
    border-bottom-left-radius: initial !important;
    border-bottom-right-radius: 10px !important;
    color: #5457b6 !important;
    padding-bottom: 20px !important;
    padding-top: 10px !important;
    width: 40% !important;
  }
}

main {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card {
  background-color: white;
  border-radius: 10px;
  padding: 10px;
  margin: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 40em;
}

.sone {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.sone button {
  border: 0px;
  height: 1.5em;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  color: #5457b6;
  padding-bottom: 20px;
  padding-top: 10px;
}

.sone button svg {
  color: #67727e;
}

.sone span + button {
  border: 0px;
  height: 1.5em;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  color: #5457b6;
  padding-top: 10px;
  padding-bottom: 25px;
}

.stwo {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 90%;
  margin: 0em 1em 0em 1em;
  align-items: stretch;
  align-content: center;
}

.sone span {
  background-color: #eaecf1;
  display: flex;
  justify-content: center;
  color: #5457b6;
  align-items: center;
}

.sone {
  width: 5%;
}

.stwoi {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.stwoii {
  display: flex;
  align-items: center;
}

.stwoii span + span {
  color: #67727e;
}

.sectionText {
  color: #67727e;
  margin: 2vh 0vh 2vh 0vh;
}

.reply {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 40em;
  padding-left: 2em;
}

.reply .card {
  width: 94%;
}

.reply::before {
  content: "";
  width: 0.01em;
  background-color: #C5C6EF;
  margin-right: 1em;
  height: 20em;
}

.Breply {
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
  width: 10vh;
  background-color: transparent;
  border: 0px;
}

.Breply span {
  color: #5457b6;
  margin-left: 1em;
}

.user {
  background-color: white;
  border-radius: 10px;
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 40em;
}

.comentar {
  background-color: white;
  display: flex;
  justify-content: space-between;
  width: 40em;
  padding: 1em;
  flex-direction: row;
  border-radius: 10px;
}

.comentar textarea {
  width: 70%;
  padding: 15px;
  resize: none;
  border-color: #eaecf1;
  border-radius: 5px;
}

:focus-visible {
  outline-style: auto;
  outline-color: #c3c4ef;
}

button:active {
  -webkit-filter: opacity(0.5);
          filter: opacity(0.5);
}

.comentar button {
  height: -webkit-min-content;
  height: min-content;
  background-color: #5457b6;
  color: white;
  border-radius: 10px;
  padding: 1em 2em;
  border: 0px;
}
